import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";

export default class landingpage extends Component {
  render() {
    return (
      <div style={{ width: "100%", margin: "auto" }}>
        <Grid className="landing-grid">
          <Cell col={12}>
            <img
              src={require("../img/myface.png")}
              alt="avatar"
              className="avatar-img"
            />

            <div className="banner-text">
              <h1>Matthew Harvey</h1>

              <hr />

              <p>
                Welcome to my react website! I am a recent computer science
                graduate at Otago University.
                <br /> I have a passion for creating websites and plan to become
                a web developer for my future career. <br />
                Below are some links to my work, click to check them out.
              </p>

              <div className="social-links">
                {/* LinkedIn */}
                <a
                  href="https://www.linkedin.com/feed/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i className="fab fa-linkedin" aria-hidden="true" />
                </a>

                {/* Github */}
                <a
                  href="https://github.com/MatthewHarv"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i className="fab fa-github-square" aria-hidden="true" />
                </a>

                {/* Youtube */}
                <a
                  href="http://matthewjharvey.com/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i className="fab fa-wordpress" aria-hidden="true" />
                </a>
              </div>
            </div>
          </Cell>
        </Grid>
      </div>
    );
  }
}
