import React, { Component } from "react";
import {
  Tabs,
  Tab,
  Grid,
  Cell,
  Card,
  CardTitle,
  CardText,
  CardActions,
  CardMenu,
  IconButton
} from "react-mdl";

export default class projects extends Component {
  constructor(props) {
    super(props);
    this.state = { activeTab: 0 };
  }

  toggleCategories() {
    if (this.state.activeTab === 0) {
      return (
        <div className="projects-grid">
          {/* Project 1 */}
          <Card
            shadow={5}
            style={{
              minWidth: "450",
              margin: "auto"
            }}
          >
            <CardTitle
              style={{
                color: "black",
                height: "176px",
                background:
                  "url(https://cdn.oceanwp.org/wp-content/uploads/2017/07/portfolio-image.png) center / cover"
              }}
            />
            <CardText>This websites source code</CardText>
            <CardActions border>
              <a
                href="https://github.com/MatthewHarv/ReactPortfolio"
                rel="noopener noreferrer"
                target="_blank"
              >
                Github React - Portfolio
              </a>
            </CardActions>
            <CardMenu
              style={{
                color: "#fff"
              }}
            >
              <IconButton name="share" />
            </CardMenu>
          </Card>

          {/* Project 2 */}
          <Card
            shadow={5}
            style={{
              minWidth: "450",
              margin: "auto"
            }}
          >
            <CardTitle
              style={{
                color: "black",
                height: "176px",
                background:
                  "url(https://images-eu.ssl-images-amazon.com/images/I/51NvZiQtk6L.png"
              }}
            >
              React To-Do-List
            </CardTitle>
            <CardText>
              A to-do-list that can add/edit/remove items in the react framework
            </CardText>
            <CardActions border>
              <a
                href="https://github.com/MatthewHarv/ReactToDoList"
                rel="noopener noreferrer"
                target="_blank"
              >
                Github React - To-Do-List
              </a>
            </CardActions>
            <CardMenu
              style={{
                color: "#fff"
              }}
            >
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );

      //##########################################################
    } else if (this.state.activeTab === 1) {
      return (
        <div className="projects-grid">
          {/* Project 1 */}
          <Card
            shadow={5}
            style={{
              minWidth: "450",
              margin: "auto"
            }}
          >
            <CardTitle
              style={{
                color: "black",
                height: "176px",
                background:
                  "url(https://upload.wikimedia.org/wikipedia/commons/thumb/6/61/HTML5_logo_and_wordmark.svg/1200px-HTML5_logo_and_wordmark.svg.png) center / cover"
              }}
            >
              HTML Projects
            </CardTitle>
            <CardText>First Year HTML Web Development</CardText>
            <CardActions border>
              <a
                href="https://github.com/MatthewHarv/Cosc112"
                rel="noopener noreferrer"
                target="_blank"
              >
                Github HTML
              </a>
            </CardActions>
            <CardMenu
              style={{
                color: "#fff"
              }}
            >
              <IconButton name="share" />
            </CardMenu>
          </Card>

          {/* Project 2 */}
          <Card
            shadow={5}
            style={{
              minWidth: "450",
              margin: "auto"
            }}
          >
            <CardTitle
              style={{
                color: "black",
                height: "176px",
                background:
                  "url(https://cdn0.froala.com/assets/editor/docs/server/meta-social/php-00441489844a0103eae71ba2fe15244c.png) center / cover"
              }}
            >
              PHP Projects
            </CardTitle>
            <CardText>Second Year PHP Web Development</CardText>
            <CardActions border>
              <a
                href="https://github.com/MatthewHarv/Cosc212"
                rel="noopener noreferrer"
                target="_blank"
              >
                Github PHP
              </a>
            </CardActions>
            <CardMenu
              style={{
                color: "#fff"
              }}
            >
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );

      //##########################################################
    } else if (this.state.activeTab === 2) {
      return (
        <div className="projects-grid">
          {/* Project 1 */}
          <Card
            shadow={5}
            style={{
              minWidth: "450",
              margin: "auto"
            }}
          >
            <CardTitle
              style={{
                color: "black",
                height: "176px",
                background:
                  "url(https://upload.wikimedia.org/wikipedia/en/thumb/3/30/Java_programming_language_logo.svg/1200px-Java_programming_language_logo.svg.png) center / cover"
              }}
            >
              Java
            </CardTitle>
            <CardText>Third Year Java Programming</CardText>
            <CardActions border>
              <a
                href="https://github.com/MatthewHarv/Cosc326"
                rel="noopener noreferrer"
                target="_blank"
              >
                Github Java
              </a>
            </CardActions>
            <CardMenu
              style={{
                color: "#fff"
              }}
            >
              <IconButton name="share" />
            </CardMenu>
          </Card>
        </div>
      );
    }
  }

  render() {
    return (
      <div>
        <Tabs
          activeTab={this.state.activeTab}
          onChange={tabId => this.setState({ activeTab: tabId })}
          ripple
        >
          <Tab>React</Tab>
          <Tab>Html/PHP</Tab>
          <Tab>Java</Tab>
        </Tabs>

        <Grid>
          <Cell col={12}>
            <div className="content">{this.toggleCategories()}</div>
          </Cell>
        </Grid>
      </div>
    );
  }
}
